<template>
  <div class="container">
    <h3>Nuevo Empleado</h3>
    <v-form @submit.prevent="saveEmployee" ref="form" lazy-validation>
      <v-text-field
        v-model="id"
        data-vv-name="id"
        label="ID"
        :error-messages="errors.collect('id')"
        v-validate="'required|min:1'"
        required
      ></v-text-field>
      <v-text-field
        v-model="name"
        data-vv-name="name"
        :error-messages="errors.collect('name')"
        v-validate="'required|min:2'"
        label="Nombre"
        required
      ></v-text-field>
      <v-text-field
        v-model="area"
        data-vv-name="area"
        :error-messages="errors.collect('area')"
        v-validate="'required|min:2'"
        label="Área"
        required
      ></v-text-field>
      <v-text-field
        v-model="email"
        data-vv-name="email"
        label="E-mail"
        :error-messages="errors.collect('email')"
        v-validate="'required|min:2'"
        required
      ></v-text-field>
      <v-text-field
        v-model="city"
        data-vv-name="city"
        :error-messages="errors.collect('city')"
        v-validate="'required|min:2'"
        label="Ciudad"
        required
      ></v-text-field>
      <v-btn @click="backToHome" color="error" class="mr-4">Regresar</v-btn>
      <v-btn type="submit" color="warning">Guardar</v-btn>
    </v-form>
  </div>
</template>
<script>
import db from "./firebaseInit";
import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate);

export default {
  $_veeValidate: {
    validator: "new"
  },
  name: "new-employee",
  data() {
    return {
      id: null,
      name: null,
      area: null,
      email: null,
      city: null,
      dictionary: {
        attributes: {
          email: "E-mail Address"
          // custom attributes
        },
        custom: {
          id: {
            required: () => "ID es requerido",
            min: "Más de 1 caracter"
          },
          name: {
            required: () => "Nombre es requerido",
            min: "Más de dos caracteres"
          },
          area: {
            required: () => "Área es requerido",
            min: "Más de dos caracteres"
          },
          email: {
            required: () => "E-mail es requerido",
            min: "Más de dos caracteres"
          },
          city: {
            required: () => "Ciudad es requerido",
            min: "Más de dos caracteres"
          }
        }
      }
    };
  },
  methods: {
    backToHome() {
      this.$router.push("/");
    },
    saveEmployee() {
      this.$validator.validate().then(valid => {
        if (valid) {
          db.collection("employees")
            .add({
              id: this.id,
              name: this.name,
              area: this.area,
              email: this.email,
              city: this.city
            })
            .then(docRef => this.$router.push("/"))
            .catch(error => console.log(error));
        }
      });
    }
  }
};
</script>